<template>
    <div id="app">
        <div class="window_con">
            <div class="title">
                班级信息
            </div>
            <div class="btn_close" @click="closeWindow">
                <img src="@/assets/icon_close@2x.png" />
            </div>
            <div class="table_con" ref="tableCon" v-loading="!dataLoaded">
                <vue-scroll :ops="ops">
                    <el-table :data="stuList" style="width: 100%" @row-click="checkStuInfo">
                        <el-table-column prop="stuName" label="学生姓名" align="center" :width="200*dpr">
                            <template slot-scope="scope">
                                <div class="table_column">
                                    <img :src="scope.row.imgUrl" class="avatar" />
                                    {{scope.row.stuName}}
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column prop="deviceMac" label="设备地址" align="center" class-name="columnStyle">

                        </el-table-column>
                    </el-table>
                </vue-scroll>
            </div>


        </div>
    </div>
</template>
<script>
    import { formatDate } from '@/assets/date.js';
    import utils from '@/assets/utils.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    export default {
        name: 'App',
        data() {
            return {
                classId: 0,
                stuList: null,
                dpr: 1,
                dataLoaded: false,
                tableHeight: 1000,
                ops: {
                    scrollPanel: {
                        scrollingX: false,
                        scrollingY: true
                    }
                },
            }
        },
        filters: {
            formatDate(time) {
                let date = new Date(time);
                return formatDate(date, 'yyyy-MM-dd hh:mm');
            }
        },
        mounted() {
            //获取url参数
            let classId = utils.getUrlPrams("classId")
            console.log(classId)
            this.dpr = localStorage.getItem("width") / 1920
            console.log(this.dpr)
            this.classId = classId ? classId : ""
            this.locadClassInfo(classId)
            this.tableHeight = this.$refs.tableCon.offsetHeight
        },
        methods: {
            checkStuInfo(row, column, event){
                console.log(row)
                let name = row.stuName
                let sex = row.stuSex
                let stuId = row.stuId
                let stuMac = row.deviceMac
                let stuNum = row.stuNum
                window.cefQuery({
                    request: "openStuModal:"+name+"&"+sex+"&"+stuId+"&"+stuMac+"&"+stuNum,   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowClassInfo",   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            locadClassInfo(classId) {
                var that = this
                axios.get(API.api_tl + 'api/selClassStus', {
                    params: {
                        classId: that.classId
                    }
                }).then(function (res) {
                    that.dataLoaded = true
                    console.log(res.data.data)
                    that.stuList = res.data.data

                }).catch(function (error) {
                    console.log(error);
                });
            },
        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }

    body{
        overflow: hidden;
    }

    #app {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #333;
        width: 100%;
        height: 100%;
    }

    .el-loading-spinner .path {
        stroke: #8BADCE !important;
    }


    .table_con {
        height: calc(100vh - 1.4rem);
        overflow: hidden;
    }

    .table_con .el-table thead {
        color: #2E4A66;
        font-size: 0.16rem;
    }

    .table_con th {
        background-color: #F5F7FA !important;
    }

    .table_con .el-table .caret-wrapper {
        height: 0.25rem;
    }

    .table_con .el-table .sort-caret.descending {
        bottom: 0.02rem;
    }

    .table_con .el-table .sort-caret.ascending {
        top: 0;
    }

    .el-table .cell {
        line-height: 0.23rem !important;
    }

    .el-table td,
    .el-table th {
        padding: 0.12rem !important;
    }
</style>
<style scoped>
    .window_con {
        box-sizing: border-box;
        width: 100%;
        height: 100vh;
        padding-bottom: 0.4rem;
        border-radius: 0.2rem;
        background: #fff;
        display: flex;
        flex-direction: column;
    }

    .footer {
        height: 10vh;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }

    .table_con {
        box-sizing: border-box;
        flex: 1;
        padding: 0 0.4rem;
    }

    .table_column,
    .el-table {
        font-size: 0.16rem !important;
        color: #2E4A66 !important;
    }
    .table_column{
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }
    .table_column img {
        width: 0.4rem;
        height: 0.4rem;
        margin-right: 0.2rem;
        border-radius: 50%;
        vertical-align: middle;
    }
</style>